


















import { Component, Vue } from 'vue-property-decorator';
import axios, { AxiosResponse, AxiosError } from 'axios';

interface Content {
    id?: number;
    content: string;
}

interface ResponseList {
  data: Content[];
  status: string;
}

export default class Crud extends Vue {
    /**
     * content
     */
    public content: Content[] = [];
    public mounted() {
    this.find();
  }

  /**
   * find
   */
  public find() {
    axios
      .get('http://192.168.100.153:8080')
      .then((response: AxiosResponse) => {
        const json: ResponseList = response.data;
        this.content = json.data;
      })
      .catch((error: AxiosError) => {
        // handle error
        alert('Something wrong');
      });
  }

  public save() {
    const content: Content = {
      content: Date.now().toString(),
    };

    axios
      .post('http://192.168.100.153:8080', content)
      .then((response: AxiosResponse) => {
        this.find();
      })
      .catch((error: AxiosError) => {
        alert('Something wrong');
      });
  }

  public update() {
    const content: Content = {
      id: 1,
      content: 'Hello word',
    };

    axios
      .put('http://192.168.100.153:8080', content)
      .then((response: AxiosResponse) => {
        this.find();
      })
      .catch((error: AxiosError) => {
        alert('Something wrong');
      });
  }

  public remove() {
    axios
      .delete('http://192.168.100.153:8080')
      .then((response: AxiosResponse) => {
        this.find();
      })
      .catch((error: AxiosError) => {
        alert('Something wrong');
      });
  }

}
